/* HOME ICONS*/

.hm-container {padding-bottom: 1.5rem;}

.icons-wrap {padding: 21px 0;

	p{
		a {color: #fff;}
	} 
	
	img {max-width: 40%; transition: transform .2s; /* Animation */}
}

.icons-wrap {padding: 20px 0; margin-bottom: 40px;}

@media (min-width: 576px) {
	.icons-wrap {
		img {max-width: 60%; 
		&:hover { transform: scale(1.2);}
			}
}
}

@media (max-width: 991px) {
.icons-wrap {
	p {margin: 0 0 1rem;}	
}
}

/* HOME SERVICES */ 

figure {margin: 0;}

.hm-feature {background: #08171a;}

@media (min-width: 1890px) {
	.hm-feature {min-height: 640px;}
}


.feature-text {padding: 3% 4% 3%; background: $grey_1; text-align: center; width: 100%; transition: all .3s ease-out;
	&.page-text {
		h2 {font-weight:500;}
	}
	&.feature-button {padding-bottom: 40px;}
	h1{color:#fff; font-size: 2.25rem; font-weight:700;	margin: .5rem 0; }
	h2{color:#fff; font-size: 1.25rem; font-weight:700; margin: 0 0 .5rem;}
	h3{font-size: 1.25rem; font-weight: 700;}
	
	.scroll-down-arrow {color: rgba(252,252,252,.7); cursor:pointer;}
}

@media (min-width: 768px) {
	.feature-text {
			&.feature-button {padding-bottom: 50px;}
			h1{font-size: 3rem;}
			h2{font-size: 2.25rem;}
		}
}

@media (min-width: 992px) {
.feature-text {width: 80%; padding: 1.25rem 4% 1.5rem; position: absolute; bottom: 0; left: 0;       text-align: left;
    margin: 0 auto; z-index: 1;  
	position: absolute;
    left: 50%;
    transform: translate(-50%); background: rgba($grey_1,.90);
	&.page-text {width: 950px;}
		&.feature-button {text-align: center;}
		h1{font-size: 3rem; margin-bottom: 0;}
		h2{font-size: 2.25rem;}
		h3{font-size: 1.25rem;}
		.scroll-down-arrow {display: none;}
}
}

@media (min-width: 1200px) {
.feature-text {width: 60%;
	&.page-text{width: 1140px;}
	h1{font-size: 3.5rem; margin-top: 0;} 
	h2{font-size: 3rem; margin-top: 0;}
	h3{margin-top: 0; font-size: 2.25rem;}
}
}

@media (min-width: 1450px) {

}

/* Home sign-up */
.join-wrap {background: rgba($green_1, .1); border-top: 10px solid $green_3; padding-bottom:  40px;
	&.join-wrap-front {
		.container-fluid {
			@include media('>=992px') {
					padding-left: 0; padding-right: 0;
			}
        }
	}
	.join-container {padding: 3% 4% 2%; background: $green_1; margin-left: -15px; margin-right: -15px; position: relative;
		&.feature-button {padding-top: 30px;}
		h2 {color: $white_5; margin: .5rem 0; font-size: 1.25rem;
			&.h2-lozenge {
					background: #82c341;
					padding: 20px 30px;
					border-radius: 40px;
					position: absolute;
					left: 50%;
					-ms-transform: translate(-50%);
					transform: translate(-50%);
					Top: -40px;
					z-index: 1;
					font-size: 1.5rem;
					width: 80%;
					text-align: center;
					transition: all .3s ease-out;
					cursor:pointer;
				}
		}
		p {color: $white_5;
		a {color: $white_5; text-decoration: underline;
			&:hover {text-decoration: none;}
		}
		}
		
	}
	.intro-container {padding: 4% 4% 2%; background: $white_5; margin-left: -15px; margin-right: -15px;
		
		.intro-para {margin-bottom: 2rem; }
		
		.about-items{
			img {max-width: 150px; margin: .5rem auto;}
		}
		
		h2 {color: $green_1; margin-top: 1rem; font-size: 1.5rem; margin-bottom: 1.5rem;
			&.h2-large {font-size: 2rem;}
			}
		p  {color: $grey_1;}
		
		.voice-items{
			.stat-wrap {border: 12px solid rgba($teal_5, .2); border-radius: 50%; width: 150px; height: 150px; margin: .5rem auto; position: relative;
			.stat-percent {font-size: 3rem; color: $teal_5; font-weight: 700; display: inline-block; text-align: left; margin-top: 20px; margin-left: -20px;
			&::after {content: "%"; color: $teal_5; font-size: 30px; display: inline-block; padding-left: 4px; position: absolute; top: 31px;
				}
			}
		}
	}
	}
}

@media (min-width: 992px) {
	.intro-para {padding-left: 11%; padding-right: 11%;}
}

.voice-items{
			.stat-col {width: 50%; padding: 15px;}
}

@media (min-width: 768px) {
.voice-items{
			.stat-col {width: 25%;}
}
	
	
.join-wrap {
		.join-container {
			h2 {font-size: 2.25rem; margin: 0 0 1rem;}
		}
	}
}

@media (min-width: 415px) {
	.join-wrap {
	.join-container {
			h2 {font-size: 2.25rem; margin: 0 0 1rem;
				}
		}
	}
}

@media (min-width: 714px) {
	.join-wrap {
	.join-container {
			h2 {
			&.h2-lozenge {width: 50%; font-size: 2rem;}
			}
		}
	}
}


@media (min-width: 992px) {
	.join-wrap {padding-right: 0; padding-left: 0; border-top: 12px solid $green_3; background-image: rgba($green_1,.1); background-image: url('https://cpactive.org.au/wp-content/themes/cpactive/images/cp-flower.svg'), url('https://cpactive.org.au/wp-content/themes/cpactive/images/cp-flower.svg');  background-size: 20%, 15%; background-position: 95% 5%, 10% 50%;  background-repeat: no-repeat;
		.join-container {margin: 0 auto; width: 80%; padding: 1.5% 4% 1.5%;
			&.feature-button {padding-top: 40px;}
			h2 {}
		}
		.intro-container {padding: 2% 4% 3%; margin: 20px auto 0; width: 80%;}
}
}

@media (min-width: 1200px) {
	.join-wrap {
		.join-container {width: 60%;}
		.intro-container {width: 60%;}
}
}

.container-fluid {
	&.home-signup {color: #fff; background: $grey_15; padding: 20px 20px 40px;
		a{
			&.light-link {color: $grey_5; text-decoration: underline;
				&:hover{color: $yellow_5;}
				&:focus{color: $red_5;}
			}
		}
		
	}
}

/*  Home News feed */

.hm-news-feed {padding-right: 30px; padding-left: 30px; 
	@include media('>=992px') {
		padding-left: 0; padding-right: 0;
			}
}

/*  Home elements section */

.home-elements {border-top: 12px solid $green_3;
	.hm-element {background: $green_1;
		.col-lg-6 {min-height: 300px; max-height: 500px; display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center; 
			&.col-img {padding: 0;
				img {width: 100%;height: 100%;
						-o-object-fit: cover;
						object-fit: cover;
					
				}	
			}
			.col-content {color: $white_5; width: 100%; padding: 1rem 4rem; text-align: center;
					@include media('>=992px') {
						padding: 1rem 15%; 
					}
				h2 {color: $white_5; margin: .5rem 0; font-size: 1.5rem;
					@include media('>=992px') {
						font-size: 1.85rem;
					}
					@include media('>=1200px') {
						font-size: 2.25rem;
					}
					&.h2-lozenge {
						@include media('<=992px') {
							width: 80%;
							position: absolute;
							top: -1.5rem;
							left: 50%;
							-ms-transform: translate(-50%);
							transform: translate(-50%);
							}
						
						background: #82c341;
						padding: 20px 30px;
						border-radius: 40px;
						z-index: 1;
						font-size: 1.2rem;
						text-align: center;
						transition: all .3s ease-out;
						cursor:pointer;
						margin: 0 auto 0;
						display: inline-block;
						@include media('>=992px') {
							margin: 1rem auto 0;
							font-size: 1.5rem;
						}
						@include media('>=1200px') {
							font-size: 2rem;
						}
					}
				}
				p {
					@include media('>=1200px') {
							font-size: 1.2rem;
						}
				}
			}
		}
	}

	.hm-element{
		&.sign-up{
			.col-content {padding: 2.5rem 4rem 1rem; position: relative;
				
					@include media('>=992px') {
							padding: 1rem 4rem;
						}
				p{
					@include media('>=1200px') {
						padding: 2.5rem 4rem;
						font-size: 1.1rem;
						}
				}
			}
		}
		&:nth-child(2n) {-ms-flex-direction: row-reverse; flex-direction: row-reverse;}
		&.share-story {background: $purple_5;}
		&.sign-petition {background: $purple_5;}
		&.events {background: $orange_15;}
	}
}

.feature-text {
		&.feature-full {
			@include media('>=992px') {width: 100%; bottom: -1px;}
	}
}










